body{
    margin:30px 20px;
    color: #bcd0f7;
    background: #1A233A;
}
.title{
    font-size:2rem;
}
.contact{
    display:flex;
    justify-content: center;
    padding:100px;
    a {
        font-size: 2rem;
        color:#bcd0f7;
        padding:30px;
        text-decoration: none;
    }
    &-box{
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border:#bcd0f7 2px solid;
        width:500px;
        height:400px;
        &_sub{
            display:flex;
            align-items: center;
        }
        
        &_logo{
            width:60px;
        }
    }
}